import { Button, Stack, Typography } from '@mui/material'
import { DataGrid } from '@mui/x-data-grid'
import { useEffect } from 'react'
import { Link, useLocation } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getWebAuthors } from 'redux/slices/WebAuthor'

export default function PageComponent() {
	const location = useLocation()
	const dispatch = useAppDispatch()
	const status = useAppSelector((s) => s.WebAuthor.status)
	const list = useAppSelector((s) => s.WebAuthor.list)
	useEffect(() => {
		if (status === 'init') dispatch(getWebAuthors())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])
	return (
		<Stack gap={1}>
			<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
				<Typography
					sx={{
						fontWeight: 600,
					}}
					variant="h5"
				>
					承認者一覧
				</Typography>
				<Link to="/author/create" state={{ location }}>
					<Button
						variant="outlined"
						sx={{
							minWidth: 120,
						}}
					>
						新規作成
					</Button>
				</Link>
			</Stack>
			<DataGrid
				rows={list}
				columns={[
					{
						field: 'email',
						headerName: '承認メール',
						flex: 1,
						renderCell: (v) => {
							return (
								<Link to={`/author/${v.row.id}`}>
									<Typography variant="caption" mr={1}>
										{v.row.email}
									</Typography>
								</Link>
							)
						},
					},
				]}
				initialState={{
					pagination: {
						paginationModel: { page: 0, pageSize: 10 },
					},
				}}
				pageSizeOptions={[10, 20, 30, 50]}
				sx={{
					'& .MuiDataGrid-cell:focus': {
						outline: 'none',
					},
					'& .MuiDataGrid-cell:focus-within': {
						outline: 'none',
					},
					'& .MuiDataGrid-cell:hover': {},
					'& .MuiDataGrid-columnHeaders': {
						bgcolor: 'primary.dark',
						color: '#fff',
					},
					'& .MuiDataGrid-columnHeaders svg': {
						color: '#fff',
					},
				}}
				density="compact"
				hideFooterSelectedRowCount
				disableColumnMenu
				disableRowSelectionOnClick
			/>
		</Stack>
	)
}
